body {
  background-color: #eceff1;
}

.show-data {
  width: 100%;
  background-color: #cfd8dc;
  margin: 20px;
  padding: 10px;
  border-radius: 6px;
  box-shadow: 1px;
}

h1.show-data {
  text-align: center;
}

img {
  display: block;
}

.show-info {
  width: 100%;
}

@media (min-width: 801px) {
  .show-data {
    display: flex;
  }

  .show-info {
    padding: 10px;
  }
}

@media (max-width: 800px) {
  img {
    margin: auto;
  }

  p.show-info {
    text-align: center;
    padding: 10px;
  }
}
